$header-height: 100px;


//Text
$font-content: Vollkorn, serif;
$font-title: Futura, 'Open Sans', Helvetica, sans-serif;//"Trebuchet MS", Helvetica, sans-serif;
$font-code: "Lucida Console", Monaco, monospace;
$font-small-header: "Trebuchet MS", Helvetica, sans-serif;

$text-color-default: #000000;
$text-color-subtle: #9b9b9b;

//Loading
$loading-anim-length: 1s;
$loading-anim-dist: 25px;
$loading-anim-size: 10px;
$loading-color: $text-color-subtle;

$header-border-color: #ddd;
