@import "variables";
@import "mixins";

.post-header {
  border-bottom: double 3px $header-border-color;
}

.post-title {
  margin: 0;
  //font-size: 1.4em;
  @include title-font();
  text-transform: uppercase;
}

.post-content {
  p {
    font-size: 1.2em;
  }

  //Headers
  h1, h2, h3, h4, h5, h6 {
    @include title-font();
  }

  h1 {
    border-bottom: double 3px $header-border-color;
  }

  h2 {
    border-bottom: solid 1px $header-border-color;
  }

  h4, h5, h6 {
    color: #777;
  }

  h6 {
    font-style: italic;
  }

  //Code
  code {
    font-family: $font-code;
    background-color: #e5e5e5;
    border-radius: 5px;
    font-size: 0.9em;
    padding: 2px;
    border: #c8c8c8 solid 1px;
  }

  pre {
    code {
      background-color: transparent;
      border: none;
      padding: 0;
    }
    padding: 20px;
    background-color: #e5e5e5;
    border-radius: 10px;
    line-height: 1.5em;
    border: #c8c8c8 solid 1px;
    overflow: scroll;
  }

  //Images
  svg {
    max-width: 100%;
  }

  img {
    margin: 0 auto;
    display: block;
    max-width: 100%;
  }
}
